<template>
  <v-container>
    <!-- animation on percentage -->
    <div>
      <h2 class="text-center mx-auto my-3">Progress With Checklist</h2>
    </div>
    <div>
      <h2 class="text-center mx-auto my-3">
        Total Progress: {{ totalper.toFixed(2) }}
      </h2>
    </div>
    <div class="mb-2">
      <v-progress-linear
        color="light-blue"
        height="25"
        :value="totalper.toFixed(2)"
        striped
      >
        <strong>{{ totalper.toFixed(2) }}%</strong>
      </v-progress-linear>
    </div>
    <v-card>
      <v-card-text>
        <v-data-table
          :items="checklists"
          :items-per-page="checklists.length"
          :loading="checklists.length < 1"
          hide-default-footer
          :headers="headers"
        >
          <template v-slot:item.id="{ item }">
            <v-btn @click="openForEdit(item)" fab small dark>
              <v-icon>mdi-circle-edit-outline</v-icon>
            </v-btn>
          </template>
          <template v-slot:item.progressper="{ item }">
            <v-progress-circular :value="item.progressper" color="green">
              <small>{{ item.progressper }}%</small>
            </v-progress-circular>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
    <v-dialog v-model="checkitem" max-width="600">
      <v-card>
        <v-card-title>
          <h2 class="mx-auto text-center">Check List Details</h2>
        </v-card-title>
        <v-card-text>
          <v-simple-table>
            <tbody>
              <tr>
                <td>Video</td>
                <td><v-checkbox v-model="selected.video" /></td>
              </tr>
              <tr>
                <td>Book</td>
                <td><v-checkbox v-model="selected.book" /></td>
              </tr>
              <tr>
                <td>Note</td>
                <td><v-checkbox v-model="selected.note" /></td>
              </tr>
              <tr>
                <td>Test Paper</td>
                <td><v-checkbox v-model="selected.testpaper" /></td>
              </tr>
              <tr>
                <td>Question Bank</td>
                <td><v-checkbox v-model="selected.qbank" /></td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" tile large @click="checkitem = false"
            >Cancel</v-btn
          >
          <v-btn
            class="primary"
            tile
            large
            @click="updateitem"
            :loading="loading"
            >Update</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      checklists: [],
      checkitem: false,
      headers: [
        { text: "Subject", value: "Subject" },
        { text: "Chapter", value: "Chapters" },
        { text: "Progress", value: "progress" },
        { text: "Percentage", value: "progressper" },
        { text: "Edit", value: "id" },
      ],
      selected: {},
      loading: false,
      totalper: 0,
    };
  },
  methods: {
    updateitem() {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbzesOKX8-ma3N26RfY3vvMoeeORSk1eX2RlKaUJsPkzR3vzcfDavZDgwZtLA77ihmi1Qg/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            sheetid: this.user.photoURL,
            ...this.selected,
            type: "progress",
          }),
        }
      ).then(() => {
        let index = this.checklists.findIndex((s) => s.id == this.selected.id);

        this.checklists[index] = { ...this.selected };
        this.loading = false;
        this.getChecklists();
        this.checkitem = false;
      });
    },
    openForEdit(item) {
      this.selected = item;
      this.checkitem = true;
    },
    getChecklists() {
      fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${this.user.photoURL}&type=progress`
      )
        .then((res) => res.json())
        .then((data) => {
          let pers = 0;
          this.checklists = data.routines.map((d) => {
            let c = this.getProgress(d);
            pers += c["progressper"];
            return c;
          });
          this.totalper = pers / this.checklists.length;
        });
    },
    getProgress(data) {
      let count = 0;
      for (const k in data) {
        if (["video", "book", "note", "qbank"].includes(k)) {
          if (data[k]) {
            count++;
          }
        }
      }

      switch (count) {
        case 1:
          data["progress"] = "More to Go";
          break;
        case 2:
          data["progress"] = "Study Hard";
          break;
        case 3:
          data["progress"] = "Keep it up";
          break;
        case 4:
          data["progress"] = "Almost There";
          break;
        case 5:
          data["progress"] = "Chapter Complete";
          break;
        default:
          data["progress"] = "No progress";
      }

      data["progressper"] = count * 20;
      return data;
    },
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
  created() {
    this.getChecklists();
  },
};
</script>

